@import "../../sass/config.scss";

.main {
    margin: 1rem;
    display: grid;
    grid-template-columns: repeat(3, 35rem);
    grid-template-rows: repeat(10, 10vh);
    justify-content: space-evenly;
}

.item1 {
    grid-column: 1;
    grid-row: 4;
}
.item2 {
    grid-column: 2;
    grid-row: 3;
}
.item3 {
    grid-column: 3;
    grid-row: 2;
}
